<template>
  <div id="home">
    <div class="cursor" id="cursor"><div class="dot"></div></div>
    <canvas
      @mousemove="draw"
      @mousedown="startDraw"
      @mouseup="stopDraw"
      @touchmove="mobileDraw"
      @touchstart="startDraw"
      @touchend="stopDraw"
      id="canvas"
      @click="
        (showPenMenu = false), (showColorMenu = false), (openClearMenu = false), (showEraserMenu = false)
      "
    >
    </canvas>
    <div class="bottom-control">
      <!--   <div class="btn" title="Undo" @click="undo">
        <object
          class="btn-svg"
          :data="svgs.leftSvg"
          type="image/svg+xml"
        ></object>
      </div>
 -->
      <div
        class="btn"
        title="Delete All"
        @click="openClearMenu = !openClearMenu"
      >
        <object
          class="btn-svg"
          :data="svgs.trashSvg"
          type="image/svg+xml"
        ></object>
        <div
          class="clear-menu"
          v-show="openClearMenu"
          :class="{ appear: openClearMenu }"
        >
          <h4>Clear everything?</h4>
          <div class="clr-btns">
            <p @click="clearCanvas">Yes</p>
            <p @click="openClearMenu = 'false'">No</p>
          </div>
        </div>
      </div>

      <div class="btn" title="Eraser" @click="showEraserMenu = !showEraserMenu">
        <object
          class="btn-svg"
          :data="svgs.eraserSvg"
          type="image/svg+xml"
        ></object>
        <div class="eraser-menu" :class="{ appear: showEraserMenu }">
          <span
            @click="
              erase();
              eraserWidth = 2;
            "
            ><p>2</p></span
          >
          <span
            @click="
              erase();
              eraserWidth = 5;
            "
            ><p>5</p></span
          >
          <span
            @click="
              erase();
              eraserWidth = 10;
            "
            ><p>10</p></span
          >
          <span
            @click="
              erase();
              eraserWidth = 20;
            "
            ><p>20</p></span
          >
          <span
            @click="
              erase();
              eraserWidth = 30;
            "
            ><p>30</p></span
          >
          <span
            @click="
              erase();
              eraserWidth = 50;
            "
            ><p>50</p></span
          >
          <span
            @click="
              erase();
              eraserWidth = 90;
            "
            ><p>90</p></span
          >
        </div>
      </div>

      <div
        class="btn"
        title="Pen Size"
        @click="(showPenMenu = !showPenMenu), eraseStop()"
      >
        <object
          class="btn-svg"
          :data="svgs.pencilSvg"
          type="image/svg+xml"
        ></object>
        <div class="pen-menu" :class="{ appear: showPenMenu }">
          <span @click="brushSize = 2"><p>2</p></span>
          <span @click="brushSize = 5"><p>5</p></span>
          <span @click="brushSize = 10"><p>10</p></span>
          <span @click="brushSize = 20"><p>20</p></span>
          <span @click="brushSize = 30"><p>30</p></span>
          <span @click="brushSize = 50"><p>50</p></span>
          <span @click="brushSize = 90"><p>90</p></span>
        </div>
      </div>

      <div
        class="btn"
        title="Choose Color"
        @click="showColorMenu = !showColorMenu"
      >
        <object
          class="btn-svg"
          :data="svgs.paintSvg"
          type="image/svg+xml"
        ></object>
        <div class="color-menu" :class="{ appear: showColorMenu }">
          <div v-for="(color, index) in colors" :key="index">
            <!-- set color palatte color | set color -->
            <div
              class="color"
              :style="{ 'background-color': color }"
              @click="this.color = color"
            ></div>
          </div>
        </div>
      </div>

      <div class="btn" title="Download The Canvas" @click="clickUpload">
        <object
          class="btn-svg"
          :data="svgs.uploadSvg"
          type="image/svg+xml"
        ></object>
        <input
          id="uploadInput"
          type="file"
          @change="uploadImage"
          hidden
          accept="image/*"
        />
        <img id="prevImg" hidden />
      </div>

      <div class="btn" title="Download The Canvas" @click="downloadCanvas">
        <object
          class="btn-svg"
          :data="svgs.dowloadSvg"
          type="image/svg+xml"
        ></object>
      </div>

      <a
        target="_blank"
        href="https://onurusluca.me/"
        class="btn"
        title="See My Portfolio"
      >
        <object
          class="btn-svg heart"
          :data="svgs.heartSvg"
          type="image/svg+xml"
        ></object>
      </a>
    </div>
  </div>
</template>

<script>
export default {
  name: "home",
  data() {
    return {
      /* Include SVGs for object data */
      svgs: {
        leftSvg: require("../assets/icons/undo.svg"),
        dowloadSvg: require("../assets/icons/download.svg"),
        heartSvg: require("../assets/icons/heart.svg"),
        paintSvg: require("../assets/icons/paint-palette.svg"),
        pencilSvg: require("../assets/icons/pen.svg"),
        trashSvg: require("../assets/icons/delete.svg"),
        uploadSvg: require("../assets/icons/upload.svg"),
        eraserSvg: require("../assets/icons/eraser.svg"),
      },
      brushSize: 10,
      eraserWidth: 10,
      color: "black",
      randomColor: "#",
      colors: [
        "#222",
        "#fff",
        "#2C3E50",
        "#5D6D7E",
        "#7F8C8D",
        "#95A5A6",
        "#CACFD2",
        "#D35400",
        "#E67E22",
        "#F39C12",
        "#F4D03F",
        "#2ECC71",
        "#27AE60",
        "#16A085",
        "#1ABC9C",
        "#3498DB",
        "#2980B9",
        "#8E44AD",
        "#9B59B6",
        "#CB4335",
        "#A93226",
      ],
      canvas: null,
      x: 0,
      y: 0,
      isDrawing: false,
      showPenMenu: false,
      showColorMenu: false,
      openClearMenu: false,
      showEraserMenu: false,
      selectedImg: null,
      isErasing: false,
    };
  },
  mounted() {
    let c = document.getElementById("canvas");
    this.canvas = c.getContext("2d");

    this.resizeCanvas();
    this.drawDots();
    this.getRandomColor();
    this.setRandomColor();
    /* hide cursor for some time because it appears on the left top on page load */
    document.querySelector("#cursor").style.display = "none";
    setTimeout(() => {
      this.setCustomCursor();
    }, 500);
  },
  methods: {
    drawLine(x1, y1, x2, y2) {
      let ctx = this.canvas;
      ctx.beginPath();
      ctx.strokeStyle = this.color;
      ctx.moveTo(x1, y1);
      ctx.lineTo(x2, y2);
      ctx.stroke();
      ctx.closePath();

      /* make draw smoother */
      ctx.lineCap = "round";
    },
    draw(e) {
      if (this.isDrawing) {
        this.drawLine(this.x, this.y, e.offsetX, e.offsetY);
        this.x = e.offsetX;
        this.y = e.offsetY;
        e.preventDefault();
      }
    },
    erase() {
      this.isErasing = true;
    },
    eraseStop() {
      this.isErasing = false;
    },
    mobileDraw(e) {
      var touch = e.touches[0];
      var mouseEvent = new MouseEvent("mousemove", {
        clientX: touch.clientX - 15,
        clientY: touch.clientY - 15,
      });
      this.draw(mouseEvent);
      this.isErasing = false;
    },
    startDraw(e) {
      this.x = e.offsetX;
      this.y = e.offsetY;
      this.isDrawing = true;
      let ctx = this.canvas;
      ctx.lineWidth = this.brushSize;

      if (this.isErasing == true) {
        ctx.beginPath();
        ctx.globalCompositeOperation = "destination-out";
        ctx.lineTo(e.pageX, e.pageY);
        ctx.stroke();
        ctx.lineWidth = this.eraserWidth;
      } else {
        ctx.globalCompositeOperation = "source-over";
      }
    },
    stopDraw(e) {
      if (this.isDrawing) {
        this.drawLine(this.x, this.y, e.offsetX, e.offsetY);
        this.x = 0;
        this.y = 0;
        this.isDrawing = false;
      }
    },
    clearCanvas() {
      let c = document.getElementById("canvas");
      const ctx = c.getContext("2d");
      ctx.clearRect(0, 0, c.width, c.height);
      this.drawDots();
    },
    drawDots() {
      let c = document.getElementById("canvas");
      let ctx = c.getContext("2d");
      var gridSize = 25;
      ctx.fillStyle = "rgba(0, 0, 0, .3)";

      for (var i = 0; i * gridSize < c.width; i++) {
        for (var j = 0; j * gridSize < c.height; j++) {
          if (i > 0 && j > 0) {
            ctx.beginPath();
            ctx.rect(i * gridSize, j * gridSize, 2, 2);
            ctx.fill();
            ctx.closePath();
          }
        }
      }
    },
    resizeCanvas() {
      let c = document.getElementById("canvas");
      c.width = window.innerWidth - 30;
      c.height = window.innerHeight - 85;
    },
    getRandomColor() {
      var letters = "0123456789ABCDEF";
      var randomColor = "";
      for (var i = 0; i < 6; i++) {
        this.randomColor += letters[Math.floor(Math.random() * 16)];
      }
      return randomColor;
    },
    setRandomColor() {
      this.color = this.randomColor;
    },
    clickUpload() {
      document.querySelector("#uploadInput").click();
    },
    uploadImage(event) {
      let dummyImg = document.querySelector("#prevImg");
      dummyImg.src = URL.createObjectURL(event.target.files[0]);
      dummyImg.onload = function () {
        URL.revokeObjectURL(dummyImg.src);
      };
      /* settime to give some time for image to be created for putImgToCanvas to use */
      setTimeout(() => {
        this.putImgToCanvas();
      }, 300);
    },
    putImgToCanvas() {
      let image = document.querySelector("#prevImg");
      var c = document.getElementById("canvas");
      var ctx = c.getContext("2d");
      /* calculate image and canvas width to try to fit the image to cover */
      let newWidth = c.width;
      const ratio = image.width / image.height;

      let newHeight = newWidth / ratio;
      if (newHeight < c.height) {
        newHeight = c.height;
        newWidth = newHeight * ratio;
      }
      const xOffset = newWidth > c.width ? (c.width - newWidth) / 2 : 0;
      const yOffset = newHeight > c.height ? (c.height - newHeight) / 2 : 0;
      ctx.drawImage(image, xOffset, yOffset, newWidth, newHeight);
      /*   ctx.drawImage(
        dummyImg,
        c.width / 1 - dummyImg.width / 2,
        c.height / 1 - dummyImg.height / 2
      ); */
    },
    downloadCanvas() {
      /* creates a png file from canvas */
      var link = document.createElement("a");
      link.download = "EgakiImage.png";
      link.href = document.getElementById("canvas").toDataURL();
      link.click();
    },
    setCustomCursor() {
      const cursor = document.querySelector("#cursor");
      document.addEventListener("mousemove", (e) => {
        cursor.setAttribute(
          "style",
          "top: " + (e.pageY - 10) + "px; left: " + (e.pageX - 10) + "px;"
        );
      });
      document.addEventListener("mousedown", () => {
        cursor.classList.add("get-reckt");
      });
      document.addEventListener("mouseup", () => {
        cursor.classList.remove("get-reckt");
      });
    },
  },
};
</script>

<style lang="scss" scoped>
#home {
  overflow: hidden;

  #canvas {
    background-color: white;
    border: 1px solid rgba(0, 0, 0, 0.3);
    margin: 2vh;
    overflow: hidden;
    cursor: none;
  }
  .cursor {
    position: absolute;
    top: 0;
    left: 0;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    border: 2px solid rgb(255, 0, 0);
    pointer-events: none;
    user-select: none;
    mix-blend-mode: difference;
    transition: opacity 1s;
    display: flex;
    justify-content: center;
    align-items: center;
    .dot {
      width: 5px;
      height: 5px;
      background: red;
      border-radius: 11px;
    }
  }
  .get-reckt {
    border: 2px solid rgb(255, 255, 255);
  }
  #prevImg {
    object-fit: cover !important;
  }
  .bottom-control {
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 8vh;
    display: flex;
    font-display: row;
    justify-content: center;
    align-items: center;
    text-align: center;
    background: #222;
    opacity: 2;
    z-index: 5;
    .btn {
      margin: 0 0.5em;
      padding: 1vh 1.2vh;
      padding-bottom: 1.2vh;
      width: 20px;
      height: 20px;
      border-radius: 20px;
      display: flex;
      justify-content: center;
      align-items: center;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;
      transition: all 200ms;
      cursor: pointer;
      user-select: none;
      &:hover {
        background: rgba(84, 83, 83, 0.524);
      }
      .btn-svg {
        width: 100%;
        height: 100%;
        margin: 0;
        padding: 0;
        cursor: pointer;
        z-index: -1;
        filter: invert(100%);
      }
      .heart {
        margin-bottom: -0.2vh;
        filter: invert(0%);
      }
    }
    .clear-menu {
      position: fixed;
      bottom: 10vh;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;
      padding: 1vh;
      padding-bottom: 0;
      border-radius: 0.5vh;
      background: white;
      box-shadow: -2px 7px 14px 0px rgba(0, 0, 0, 0.75);
      display: none;
      opacity: 0;
      cursor: default;
      h4 {
        margin: 0;
      }
      .clr-btns {
        display: flex;
        flex-direction: row;
        p {
          padding: 0vh 2vh;
          border: 2px solid #333;
          margin: 2vh 1vh;
          border-radius: 0.5vh;
          cursor: pointer;

          &:first-child {
            color: red;
            &:hover {
              background: rgb(255, 0, 0);
              color: white;
            }
          }
          &:hover {
            background: rgba(113, 112, 112, 0.338);
          }
        }
      }
    }
    .eraser-menu {
      position: fixed;
      bottom: 10vh;
      display: flex;
      flex-wrap: wrap;
      flex-direction: row;
      justify-content: space-evenly;
      align-items: center;
      border-radius: 0.5vh;
      background: rgb(243, 134, 134);
      box-shadow: -2px 7px 14px 0px rgba(0, 0, 0, 0.75);
      display: none;
      opacity: 0;

      span {
        &:hover {
          background: rgba(113, 112, 112, 0.338);
        }
        &:first-child {
          padding-left: 2vh;
        }
        &:last-child {
          padding-right: 2vh;
        }
        padding: 0 1.2vh;
        p {
          font-weight: bold;
        }
      }
    }
    .pen-menu {
      position: fixed;
      bottom: 10vh;
      display: flex;
      flex-wrap: wrap;
      flex-direction: row;
      justify-content: space-evenly;
      align-items: center;
      border-radius: 0.5vh;
      background: white;
      box-shadow: -2px 7px 14px 0px rgba(0, 0, 0, 0.75);
      display: none;
      opacity: 0;

      span {
        &:hover {
          background: rgba(113, 112, 112, 0.338);
        }
        &:first-child {
          padding-left: 2vh;
        }
        &:last-child {
          padding-right: 2vh;
        }
        padding: 0 1.2vh;
        p {
          font-weight: bold;
        }
      }
    }
    .color-menu {
      position: fixed;
      bottom: 10vh;
      margin-left: auto;
      margin-right: auto;
      left: 0;
      right: 0;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
      opacity: 0;
      display: none;

      .color {
        width: 5vh;
        height: 4vh;
        transition: 200ms;
        box-shadow: -2px 5px 10px 0px rgba(0, 0, 0, 0.65);

        &:hover {
          transform: scale(1.2);
        }
      }
    }
  }

  .appear {
    animation: popup 0.5s forwards cubic-bezier(0.2, 2, 0.4, 1);
    display: flex !important;
  }
  @keyframes popup {
    from {
      opacity: 0;
      transform: translateX(40px);
    }
    to {
      opacity: 1;
      transform: none;
    }
  }
  $xs-mobile: "only screen and (max-width: 480px)";
  $s-tablets: "only screen and (min-width: 481px) and (max-width: 767px)";
  @media #{$xs-mobile, $s-tablets} {
    .cursor {
      display: none;
    }
    .bottom-control {
      .btn {
        margin: 0 .5vh;
        width: 25px;
        height: 25px;
      }
      .eraser-menu {
          width: 90%;
        margin-left: auto;
        margin-right: auto;
        left: 0;
        right: 0;
      }
      .pen-menu {
        width: 90%;
        margin-left: auto;
        margin-right: auto;
        left: 0;
        right: 0;
      }
      .color-menu {
        width: 90%;
        .color {
          width: 7vh;
          height: 5vh;
          margin: 0.5vh;
          border-radius: 3px;
        }
      }
    }
  }
}
</style>